.app-wrapper {
  display: grid;
  grid-template-areas:"one one"
                      "two three";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: three;
  background: center url("https://a-static.besthdwallpaper.com/icons-pattern-design-wallpaper-2560x1080-79173_14.jpg");
  background-size: 100%;
}

div {
  color: #00ff1f;
}